html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body {
  background-color: #bf1650;
  font-family: Lato, sans-serif;
}

.container {
  max-width: 500px;
  flex-direction: column;
  justify-content: center;
  margin: 30px auto 150px;
  display: flex;
}

.main-container {
  background-color: #081229;
  border-radius: 6px;
  margin: 10px;
  padding: 50px;
  box-shadow: 0 18px 50px -10px #0003;
}

.header {
  padding: 20px;
}

.header__heading {
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  word-spacing: 3px;
  font-size: 40px;
}

.form {
  color: #fff;
  flex-direction: column;
  display: flex;
}

.form__label {
  letter-spacing: 2px;
  font-size: 18px;
}

.form__input {
  margin-top: 15px;
  padding: 10px;
}

.form__list {
  height: 200px;
  margin: 0;
  padding: 30px 0;
  list-style: none;
}

.form__list-error {
  color: #dc143c;
  margin: 0;
  padding: 5px 0;
  font-size: 16px;
}

.form__button {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  background-color: #bf1650;
  border: none;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  padding: 20px 30px;
  font-size: 18px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 10px 36px #00000029, 0 0 0 1px #0000000f;
}

.form__button:hover {
  font-weight: 700;
  transform: scale(1.05);
}

.form__message {
  color: #bf1650;
  text-align: center;
  margin-top: 60px;
  padding: 20px;
  font-size: 30px;
}

.form__information {
  color: #fff;
  letter-spacing: 2px;
  font-size: 14px;
}

.footer {
  width: 100%;
  color: #fff;
  text-align: center;
  background-color: #081229;
  padding: 40px 0;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
}

